<template>
  <div>
    <van-notice-bar
        wrapable
        :scrollable="false"
        text="请确认后台有匹配下单金额及支付方式的有效广告，否则将充值失败！"
    />
    <div style="line-height: 24px;padding:24px">
      <div>Hi,{{name}}</div>
      <div>您的余额为：{{Balance}}元</div>
    </div>
    <van-tabs type="card" @change="onChange">
      <van-tab title="充值">
        <van-radio-group v-model="payMod">
          <van-cell-group  title="选择支付方式">
            <van-cell title="银行卡转账" clickable @click="payMod = '15'">
              <template #right-icon>
                <van-radio name="15" />
              </template>
            </van-cell>
            <van-cell title="支付宝" clickable @click="payMod = '13'">
              <template #right-icon>
                <van-radio name="13" />
              </template>
            </van-cell>
            <van-cell title="微信" clickable @click="payMod = '14'">
              <template #right-icon>
                <van-radio name="14" />
              </template>
            </van-cell>
          </van-cell-group>
        </van-radio-group>
        <van-cell-group title="充值金额">
          <van-field v-model="value"  placeholder="请输入充值金额"  type="number"/>
          <div style="padding:8px 16px">
            <van-tag plain type="warning" @click="value=10" style="margin-right: 8px">10</van-tag>
            <van-tag plain type="warning" @click="value=20" style="margin-right: 8px">20</van-tag>
            <van-tag plain type="warning" @click="value=50" style="margin-right: 8px">50</van-tag>
            <van-tag plain type="warning" @click="value=100" style="margin-right: 8px">100</van-tag>
            <van-tag plain type="warning" @click="value=200" style="margin-right: 8px">200</van-tag>
            <van-tag plain type="warning" @click="value=500" style="margin-right: 8px">500</van-tag>
            <van-tag plain type="warning" @click="value=1000" style="margin-right: 8px">1000</van-tag>
          </div>
        </van-cell-group>
<!--        <div style="margin-top: 24px;padding:0 16px">-->
<!--          <van-button-->
<!--              type="danger"-->
<!--              block-->
<!--              @click="showAccount" >-->
<!--            我要充值-->
<!--          </van-button>-->
<!--        </div>-->
        <div style="margin-top: 24px;padding:0 16px">
          <van-button
              type="danger"
              block
              @click="deposit" >
            我要充值
          </van-button>
        </div>
        <van-dialog v-model="show" title="转账账户" show-cancel-button @confirm="deposit">
          <van-cell-group title="请填写您的付款账户，请确保与您实际转账账户一致，否则会影响您的充值到账！">
            <van-field v-model="accountName" label="付款账号名"  placeholder="请输入您的付款账号名" />
            <van-field v-model="account" label="付款账号" placeholder="请输入您的付款账号"  />
          </van-cell-group>
        </van-dialog>
        <van-dialog v-model="showDeposit" title="付款账户" confirm-button-text="我已支付" show-cancel-button @confirm="confirmPay(orderResult.sBusinessOrderId)" @cancel="cancelPay(orderResult.sBusinessOrderId)">
          <van-cell-group :title="title">
            <van-cell title="充值类型">
              <div v-if="payMod ==='15'">银行卡</div>
              <div v-if="payMod ==='13'">支付宝</div>
              <div v-if="payMod ==='14'">微信</div>
            </van-cell>
            <van-cell title="收款账户名">{{orderResult.stPayMode.sName}}</van-cell>
            <van-cell title="收款账号">{{orderResult.stPayMode.sAccount}}</van-cell>
            <van-cell >
              <div style="text-align: center">
                <van-image width="50%" v-if="orderResult.stPayMode.sAttachmentUrl && orderResult.stPayMode.sAttachmentUrl !==''" :src="orderResult.stPayMode.sAttachmentUrl" />
                <div>付款二维码</div>
              </div>
            </van-cell>
          </van-cell-group>
        </van-dialog>
        <van-dialog v-model="isNotConfirmPay" title="已确认支付，等待到账"  @confirm="handleOk" >
<!--          <van-cell-group>-->
<!--          </van-cell-group>-->
        </van-dialog>



      </van-tab>
      <van-tab title="充值记录">
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
          <van-list
              v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              @load="onLoad"
          >
            <van-panel style="margin-top: 8px" v-for="item in list" :key="item.orderId" :title="item.createTime"  :desc="item.orderId" :status="initStatus(item.orderStatus)">
              <van-cell title="充值金额" :border="false">{{item.rechargeAmount}}</van-cell>
              <van-cell title="转账方式" :border="false">
                <span v-if="item.payMode ===13">支付宝</span>
                <span v-if="item.payMode ===14">微信</span>
                <span v-if="item.payMode ===15">银行卡</span>
              </van-cell>
              <van-cell title="收款账户"  :border="false">
                {{item.receiveAccount}}
              </van-cell>
              <van-cell title="收款人"  :border="false">
                {{item.receiveName}}
              </van-cell>
              <template #footer v-if="item.orderStatus === 1 ">
                <div style="text-align: right">
                  <van-button size="small" style="margin-right: 8px" @click="cancelPay(item.id)">取消充值</van-button>
                  <van-button size="small" type="danger" @click="confirmPay(item.id)">已充值</van-button>
                </div>
              </template>
            </van-panel>
          </van-list>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import {postAction} from "@/api";
import {gpayMixin} from "@/view/gpay/mixin";
import {MSG_TIPS} from "@/utils/gpay/msgCode";
import {mapGetters} from 'vuex'
import {
  ActionSheet, Area,
  Button, Dialog,
  Cell,
  CellGroup, Circle,
  Col,
  DatetimePicker,
  Divider,
  Field,
  Icon, Notify,
  Picker, Popup, Radio,
  RadioGroup,
  Row, SwipeCell, Switch, Toast,Checkbox,
  Tab,Tabs,List,
  Tag,PullRefresh,Panel,Image,NoticeBar
} from "vant";

export default {
  name: "pay",
  mixins:[gpayMixin],
  components:{
    [Col.name]: Col,
    [Row.name]: Row,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Button.name]: Button,
    [Field.name]: Field,
    [Picker.name]: Picker,
    [ActionSheet.name]: ActionSheet,
    [DatetimePicker.name]: DatetimePicker,
    [Divider .name]: Divider,
    [RadioGroup .name]: RadioGroup,
    [Radio.name]: Radio,
    [Switch.name]: Switch,
    [Popup.name]: Popup,
    [Area.name]: Area,
    [Dialog.Component.name]: Dialog.Component,
    [SwipeCell.name]: SwipeCell,
    [Notify.name]: Notify,
    [Toast.name]: Toast,
    [Circle.name]: Circle,
    [Checkbox.name]: Checkbox,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Tag.name]: Tag,
    [List .name]: List ,
    [PullRefresh.name]: PullRefresh  ,
    [Panel .name]: Panel  ,
    [Image .name]: Image  ,
    [NoticeBar.name]: NoticeBar ,
  },

  data(){
    return {
      payMod:'15',
      value:null,
      show:false,
      showDeposit:false,
      accountName:'',
      account:'',
      depositList:[],
      orderResult:{
        stPayMode:{

        }
      },
      isNotConfirmPay:false,

      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      current:1
    }
  },

  computed:{
    title(){
      return '请使用账户名:'+this.accountName+',账户号：'+this.account+'向以下账户转账'+this.value+'元！，支付完成后点击已转账'
    }
  },

  methods:{
    showAccount(){
      this.show=true
      this.accountName = ''
      this.account = ''
    },
    deposit(){
      // debugger
      const params={
        iPayMode:Number(this.payMod),
        dTradeAmount:this.value,
        // sRealName:this.accountName,
        // sAccount:this.account,
        sRealName:'',
        sAccount:'',
        iIsFastTrading:0,
        userName:this.name,
        iGoodsId:1,
      }
      // postAction('/v1/merchant/CreateMerchantOrder',params)
      // .then(res=>{
      //   if(res.success){
      //     debugger
      //     const result = JSON.parse(res.result.result)
      //     if(result.result === false){
      //       Notify({type: 'danger', message: MSG_TIPS(result.error_code)})
      //     }else{
      //       this.orderResult = result
      //       this.showDeposit = true
      //     }
      //   }
        postAction('/v1/merchant/CreateMerchantOrder',params)
      .then(res=>{
        if(res.success){
          // debugger
          const result = JSON.parse(res.result.result)
          if(res.result.code === 0){
            // debugger
            const {sAccessToken,sAccessURL,sOrderId} = result
            const url = sAccessURL + '?id=' + sOrderId + '&tk=' + sAccessToken
            // const url = 'http://localhost:8081/#/pay' + '?id=' + sOrderId + '&tk=' + sAccessToken
            this.$router.push({name:'payOrderFrame',params:{url:url}})
          }else{
            Notify({type: 'danger', message: MSG_TIPS(res.result.code)})
          }
        }
        // debugger
      })
    },
    onChange(){
      this.list=[]
      this.finished = false
      this.current = 1
      this.getDepositList()

    },
    getDepositList(){
      const params = {
        queryparams:{
          userName: this.name,
        },
        pageNo : 1,
        pageSize : 10,
      }
      postAction('/v1/userorder/QueryRechargRecord',params)
          .then(res=>{
            if(res.success){
              this.depositList = res.result
            }
          })
      // const params = {
      //   "iOrderProcess":0,
      //   "iOrderType":2,
      //   "iRoleType":0,
      //   "iPageNo":0,
      //   "iPageSize":100
      // }
      //
      // postAction('/v1/merchant/ListMerchantOrder',params)
      // .then(res=>{
      //   // debugger
      //   if(res.success){
      //     const result = JSON.parse(res.result.result)
      //     this.depositList = result.vOrderData
      //   }
      // })

    },
    confirmPay(id){
      const params={
        sBusinessOrderId:id,
        iPayMethodId:Number(this.payMod),
      }
      postAction('/v1/merchant/PayOrder',params)
          .then(res=>{
            if(res.success) {
              // debugger
              const result1 = JSON.parse(res.result)
              const result = JSON.parse(result1.result)
              if(result.result === false){
                Notify({type: 'danger', message: MSG_TIPS(result.error_code)})
              }else{
                Notify({type: 'success', message: '已确认支付'})
                this.isNotConfirmPay = true
                this.onRefresh()
              }
            }else{
              Notify({type: 'danger', message: res.message})
            }
            //
          })
    },
    cancelPay(id){
      const params={
        sBusinessOrderId:id,
      }
      postAction('/v1/merchant/CancelOrder',params)
          .then(res=>{
            if(res.success) {
              const result = JSON.parse(res.result.result)
              if(result.result === false){
                Notify({type: 'danger', message: MSG_TIPS(result.error_code)})
              }else{
                Notify({type: 'success', message: '已取消'})
                this.payMod = '15'
                this.value = null
                this.show = false
                this.accountName = ''
                this.account = ''
                this.orderResult = {stPayMode:[]}
                this.showDeposit = false
                this.onRefresh()
              }
            }else{
              Notify({type: 'danger', message: res.message})
            }
            //
          })
    },
    handleOk(){
      this.payMod = '15'
      this.value = null
      this.show = false
      this.accountName = ''
      this.account = ''
      this.orderResult = {stPayMode:[]}
      this.isNotConfirmPay = false
      this.showDeposit = false
    },

    onLoad() {
      const params = {
        queryparams:{
          userName: this.name,
        },
        pageNo : this.current ,
        pageSize : 10,
      }
      postAction('/v1/userorder/QueryRechargRecord',params)
          .then(res=>{
            if(res.success){
              this.depositList = res.result
              this.list = this.list.concat(this.depositList.records)
              if(this.list.length >= this.depositList.total){
                this.finished = true
              }
              this.current +=1
              this.refreshing = false
              this.loading = false
            }
          })

      // setTimeout(() => {
      //   if (this.refreshing) {
      //     this.list = [];
      //     this.refreshing = false;
      //   }
      //
      //   for (let i = 0; i < 10; i++) {
      //     this.list.push(this.list.length + 1);
      //   }
      //   this.loading = false;
      //
      //   if (this.list.length >= 40) {
      //     this.finished = true;
      //   }
      // }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;
      this.list = []
      this.current = 1

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    initStatus(status){
      switch (status) {
        case 1:
          return '等待充值'
        case 2:
          return '等待到账'
        case 3:
          return '申诉中'
        case 4:
          return '放币超时'
        case 10:
          return '已完成'
        case 11:
          return '已取消'
        case 12:
          return '支付超时取消'
      }
    }
  }
}
</script>

<style scoped>

</style>