import {postAction} from "@/api";
import {mapGetters} from 'vuex'
export const gpayMixin = {
    props: {

    },
    data(){
      return {
         test:false,
          getValueInterval:null,
          name:this.userName(),
      }
    },
    methods:{
        ...mapGetters(['userName','balance']),
        getBalance(){
            const params={
                userName:this.name
            }
            postAction('/v1/useramount/QueryAppAccountBalance',params)
                .then(res=>{
                    if(res.success){
                        const result = JSON.parse(res.result)
                        this.$store.commit('SET_LEFTVALUE',result.accountBalance)
                    }
                    // debugger
                })
        }
    },
    computed:{
        Balance(){
            return Number(this.balance()).toFixed(2)
        }
    },
    created(){
       if(!this.test && this.$store.getters.userName === ''){
           this.$router.push('/gpay/login')
       }else{
           this.getBalance()
           this.getValueInterval = setInterval(()=>{
               this.getBalance()
           },3000)
       }
    },
    destroyed(){
        if(this.getValueInterval)clearInterval(this.getValueInterval)
    }
}